/* start for main */
main {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  grid-column-gap: 7px;
  grid-row-gap: 7px;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: transform 1s;
  max-width: 280px;
  margin: auto;
  text-align: center;
  /* font-family: arial; */
}

.card:hover {
  transform: translateY(-10px);
}

.card img {
  width: 70%;
  height: 250px;
}

.card .price {
  color: grey;
  font-size: 22px;
}

.card .description {
  padding-left: 10px;
  padding-right: 10px;
  text-align: left;
}

.card a {
  color: black;
  text-decoration: none;
}

.card a :hover {
  opacity: 0.7;
}

.user {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 0px;
  grid-auto-rows: -webkit-min-content;
  grid-auto-rows: min-content;
}

.user ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.user li {
  padding: 5px;
  margin: 5px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.user input[type="text"],
select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.user input[type="submit"] {
  width: 100%;
  background-color: black;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.user input[type="submit"]:hover {
  opacity: 0.7;
}

.cart {
  width: 50rem;
  max-width: 90%;
  margin: 2rem auto;
}

/* .cart p {
  text-align: center;
} */

.cart ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.cart li {
  padding: 1rem;
  margin: 1rem 0;
  border: 1px solid;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#cartNav {
  transition: 0.1s;
}

.order p {
  text-align: left !important;
}

.order input[type="submit"],
button {
  width: 100%;
  background-color: black;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.cart {
  width: 50rem;
  max-width: 90%;
  margin: 2rem auto;
}

.cart p {
  text-align: center;
}

.cart ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.cart li {
  padding: 1rem;
  margin: 1rem 0;
  border: 1px solid;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#cartNav {
  transition: 0.1s;
}

.details {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 0px;
  grid-auto-rows: -webkit-min-content;
  grid-auto-rows: min-content;
}

.details ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.details li {
  padding: 5px;
  margin: 5px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.details .imgContainer {
  width: 250px;
  /* height: 250px; */
}

.details img {
  width: 90%;
  /* height: 250px; */
}

html,
body {
  /* font-size: 40px;       */
  width: 100%; /* take full browser width */
  height: 100%; /* take full browser height*/
  font-family: "Raleway", sans-serif;
}
@media screen and (min-width: 1024px) {
  #content {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-auto-rows: minmax(50px, auto);
    grid-gap: 10px;
    max-width: 1024px;
    margin: 0 auto;
    grid-template-areas:
      "header header header header header header"
      "aside main main main main main"
      "aside main main main main main"
      "footer footer footer footer footer footer";
  }
}
/* mobile grid */
@media screen and (max-width: 480px) {
  #content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(50px, auto);
    grid-gap: 10px;
    grid-row-gap: 7px;
    max-width: 480px;
    margin: 0 auto;
    grid-template-areas:
      "header header header header"
      "aside aside aside aside"
      "main main main main"
      "footer footer footer footer";
  }
}
#content > * {
  /* background: lightgray; */
  padding: 10px;
}
header {
  grid-area: header;
  position: fixed;
  background-color: white;
  top: 0;
  width: 95%;
  max-width: 1024px;
  z-index: 1;
}
main {
  grid-area: main;
  margin-top: 30px;
  margin-bottom: 30px;
}
aside {
  grid-area: aside;
  text-align: left;
  margin-top: 60px;
}
footer {
  grid-area: footer;
  text-align: center;
  position: fixed;
  background-color: white;
  bottom: 0;
  width: 100%;
  z-index: 1;
}

footer span {
  font-family: "Special Elite", cursive;
}

input[type="text"],
select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

a {
  color: black;
}

